
.category
  width: 90vw
.category .q-tabs__arrows
  color: #f4f4f4
  background: #f4f4f4
.category .q-tab
  color: #c6cfe1
  margin: 0 10px
  border: 2px solid #c6cfe1
  border-radius: 4px
.category .q-tab--active
  background: #0053ab
  color: white
  border: none

  .q-link
  text-decoration: none
  color: black
  border: 0.5px solid #f2f2f2
.q-link:hover
  border: 0.5px solid #e0e0e0
